.popup__submit {
    margin: 0 auto;
    padding: 0;
    width: 358px;
    height: 50px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.21;
    text-align: center;
    /* box-sizing: border-box; */
    border-radius: 2px;
    border: none;
    background: #000000;
    color: #fff;
    cursor: pointer;

}

.popup__submit:hover {
    opacity: 0.8;
}

@media screen and (max-width: 618px) {
    .popup__submit {
        width: 238px;
        height: 46px;
        font-size: 14px;
        line-height: 1.2;
    }
}